body {
    font: normal 18px/1.5 "Fira Sans", "Helvetica Neue", sans-serif;
    background-color: tomato;
    color: rgb(0, 0, 0);
  }
  
  .container {
    width: 80%;
    max-width: 100vw;
    margin: 0 auto;
  }
  
  .container * {
    box-sizing: border-box;
  }
  
  
  .flex-outer li,
  .flex-inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  
  
  .flex-outer > li:not(:last-child) {
    margin-bottom: 6px;
  }
 
  
  .flex-outer li label,
  .flex-outer li p {
    padding: 2px;
    font-weight: 300;
    letter-spacing: .09em;
    text-transform: uppercase;
  }
  
  .flex-outer > li > label,
  .flex-outer li p {
    flex: 1 0 120px;
    max-width: 220px;
  }
  
  .flex-outer > li > label + *,
  .flex-inner {
    flex: 1 0 220px;
  }
  
  .flex-outer li p {
    margin: 0;
  }
  
  .flex-outer li input:not([type='checkbox']),
  .flex-outer li textarea {
    padding: 15px;
    border: none;
  }
  
  .flex-outer li button {
    margin-left: auto;
    padding: 8px 16px;
    border: none;
    background: #333;
    color: #f2f2f2;
    text-transform: uppercase;
    letter-spacing: .09em;
    border-radius: 2px;
  }
  .flex-outer li button:hover{
      background-color: gray;
  }


  button{
    margin-left: auto;
